<script setup>
import { reactive, watch } from 'vue'
import { useIntegrationTremendous } from '@/store/pinia/integration/tremendeous'
import { tryCatchRequest } from '@/utils/request'

import Checkbox from 'bbUi/components/checkbox'
import Input from 'bbUi/components/input'
import Alert from 'bbUi/components/alert'
import Fieldset from 'bbUi/components/fieldset'
import Pagination from '@/components/Pagination.vue'
import Accordion from '@/components/Accordion.vue'

const integrationTremendousStore = useIntegrationTremendous()
const state = reactive({
	location: {
		page: 1,
		limit: 10,
		search: ''
	}
})

const onSearch = () => {
	state.location.page = 1
}

watch(
	() => state.location,
	async () => {
		await tryCatchRequest(integrationTremendousStore.$state.getLocations, () =>
			integrationTremendousStore.$getLocations({
				params: state.location
			})
		)
	},
	{
		deep: true
	}
)
</script>

<template>
	<div class="space-y-4">
		<div class="border border-solid border-grey-01 border-1 rounded-lg space-y-4 p-4">
			<Input
				v-model="integrationTremendousStore.$state.form.description"
				type="textarea"
				label="Payout Description"
				class="w-full"
				rows="6"
			/>
			<Accordion>
				<template #header>
					<p class="font-semibold">Where will this payout description appear?</p>
				</template>
				<template #default>
					<p class="mb-4">
						The payout description will be shown on the widget during checkout after the customer
						selects the 'Choose Later' payout method.
					</p>
					<img
						src="@/assets/tremendous-description-appear.png"
						alt="Tremendous Description Appearance" />
				</template>
			</Accordion>
		</div>
		<Fieldset class="rounded-lg">
			<template #label>Location</template>
			<template #default>
				<div class="space-y-4">
					<Alert color="blue" view="smooth" dense>
						<template #left>
							<span class="material-icons" style="font-size: 12px !important">info</span>
						</template>
						{{ integrationTremendousStore.$state.form.selectedLocations.length }} store locations
						use this Tremendous add-ons
					</Alert>
					<Input
						v-model="state.location.search"
						placeholder="Search..."
						class="w-full"
						@update:model-value="onSearch"
						icon>
						<template #icon>
							<span class="material-icons">search</span>
						</template>
					</Input>
					<Table
						:data="integrationTremendousStore.getLocations.result.data"
						:page="state.location.page"
						:limit="state.location.limit">
						<TableColumn prop="id" label="" width="100">
							<template #default="scope">
								<Checkbox
									v-model="integrationTremendousStore.$state.form.selectedLocations"
									:value="scope.row.id"
									type="toggle" />
							</template>
						</TableColumn>
						<TableColumn prop="name" label="Name" />
						<TableColumn prop="email" label="Email" />
						<TableColumn prop="address_text" label="Location" />
						<TableColumn prop="phone" label="Phone" />
						<TableColumn prop="is_default_mail_in" label="Type">
							<template #default="scope">
								{{ scope.row.is_default_mail_in === 1 ? 'Mail-In' : 'In-Store' }}
							</template>
						</TableColumn>
					</Table>
					<Pagination
						v-model:page="state.location.page"
						v-model:limit="state.location.limit"
						:data="integrationTremendousStore.getLocations.result" />
				</div>
			</template>
		</Fieldset>
	</div>
</template>
