<script setup async>
import { reactive } from 'vue'
import { useIntegrationTremendous } from '@/store/pinia/integration/tremendeous'

import Tab from 'bbUi/components/tab'
import IntegrationTremendousConfiguration from '@/components/Features/Integration/IntegrationTremendous/IntegrationTremendousConfiguration/IntegrationTremendousConfiguration.vue'
import IntegrationTremendousPayout from '@/components/Features/Integration/IntegrationTremendous/IntegrationTremendousPayout/IntegrationTremendousPayout.vue'

const integrationTremendousStore = useIntegrationTremendous()

const state = reactive({
	selectedTab: IntegrationTremendousPayout,
	tabs: [
		{
			name: 'Payouts',
			component: IntegrationTremendousPayout
		},
		{
			name: 'Configuration',
			component: IntegrationTremendousConfiguration
		}
	]
})

await Promise.all([
	!integrationTremendousStore.$state.get.result.products && integrationTremendousStore.$get(),
	integrationTremendousStore.$getLocations({
		params: {
			page: 1,
			limit: 10,
			search: ''
		}
	})
])
</script>

<template>
	<Tab v-model="state.selectedTab" :items="state.tabs" color="teal" view="smooth" component />
	<component :is="state.selectedTab" />
</template>
