<script setup>
import { computed, reactive } from 'vue'
import { useIntegrationTremendous } from 'store/pinia/integration/tremendeous'

import Alert from 'bbUi/components/alert'
import Checkbox from 'bbUi/components/checkbox'
import Input from 'bbUi/components/input'
import Tab from 'bbUi/components/tab'
import Table from '@/components/Table.vue'

const integrationTremendousStore = useIntegrationTremendous()
const state = reactive({
	search: '',
	selectedTab: 'all_categories',
	tabs: [
		{
			name: 'All categories',
			component: 'all_categories'
		},
		{
			name: 'Prepaid cards',
			component: 'prepaid_cards'
		},
		{
			name: 'Gift cards',
			component: 'gift_cards'
		},
		{
			name: 'Monetary options',
			component: 'monetery_options'
		},
		{
			name: 'Charities',
			component: 'charity'
		}
	]
})

const filteredPayouts = computed(() =>
	integrationTremendousStore.$state.get.result.products.filter(
		product =>
			(state.selectedTab === 'all_categories' ? true : product.category === state.selectedTab) &&
			(!state.search ? true : product.name.toLowerCase().includes(state.search.toLowerCase()))
	)
)
</script>

<template>
	<div class="space-y-4">
		<div class="border border-solid border-grey-01 border-1 rounded-lg space-y-4 p-4">
			<p class="text-lg font-semibold">Configuring payout Options for Offers</p>
			<p>
				Enable the payout methods you want to offer your customers. These will be visible to them
				when they are creating an offer. Customers will receive an automated email once you mark an
				offer complete, containing a link here they can choose one of your active payouts based on
				the monetary value and the min/max limits of the selected method.
			</p>
			<p>
				All payouts shown in the table are filtered based on this account's region and currency.
			</p>
			<Alert color="blue" view="smooth" dense>
				<template #left>
					<span class="material-icons" style="font-size: 12px !important">lightbulb</span>
				</template>
				<b>Pro Tip</b>
				: If you have PayPal (for example) or any other payout method enabled in your Reusely Payout
				settings and it is also offered by Tremendous, we recommend disabling it if you plan to use
				Tremendous.
			</Alert>
		</div>
		<Tab v-model="state.selectedTab" :items="state.tabs" color="teal" view="smooth" component />
		<Input v-model="state.search" placeholder="Search..." class="w-full" icon>
			<template #icon>
				<span class="material-icons">search</span>
			</template>
		</Input>
		<Table :data="filteredPayouts">
			<TableColumn prop="id" label="" width="100">
				<template #default="scope">
					<Checkbox
						v-model="integrationTremendousStore.$state.form.selectedProducts"
						:value="scope.row.id"
						type="toggle" />
				</template>
			</TableColumn>
			<TableColumn prop="name" label="Name">
				<template #default="scope">
					<div class="flex items-center gap-2">
						<div class="flex items-center h-6 w-8 border border-solid border-grey-02 rounded">
							<img v-if="scope.row.image" :src="scope.row.image.src" :alt="`Image ${scope.row.name}`"
									 class="object-contain object-center">
						</div>
						<span>{{ scope.row.name }}</span>
					</div>
				</template>
			</TableColumn>
			<TableColumn prop="range_value" label="Value Range">
				<template #default="scope">
					Min: {{ scope.row.range_value.min }}, Max: {{ scope.row.range_value.max }}
				</template>
			</TableColumn>
		</Table>
	</div>
</template>
